import React, { useState, useEffect, useMemo } from "react";
import * as handpose from "handtrackjs";
import { GameContainer } from "../GameContainer/GameContainer";
import { isMobile } from "react-device-detect";
import "./WebCam.css";

const SIZE = 500;

const StreamVideo = () => {
  var video = document.querySelector("#video");
  video.width = SIZE;
  video.height = SIZE;

  if (navigator.mediaDevices.getUserMedia) {
    navigator.mediaDevices
      .getUserMedia({ video: true })
      .then(function (stream) {
        video.srcObject = stream;
      })
      .catch(function (error) {
        console.log(
          "Something went wrong! Make sure you have webcam enabled",
          error
        );
      });
  }

  return video;
};

export const WebCam = () => {
  const [model, setModel] = useState(undefined);

  useEffect(() => {
    const modelParams = {
      flipHorizontal: true, // flip e.g for video
      imageScaleFactor: 1, // reduce input image size .
      maxNumBoxes: 1, // maximum number of boxes to detect
      iouThreshold: 0.5, // ioU threshold for non-max suppression
      scoreThreshold: 0.85, // confidence threshold for predictions.
    };

    handpose.load(modelParams).then((model) => {
      setModel(model);
      StreamVideo();
    });
  }, []);

  const isMobileDevice = useMemo(() => {
    if (isMobile) {
      return true;
    } else return false;
  }, []);

  return <GameContainer model={model} isMobileDevice={isMobileDevice} />;
};
